@import "./../../common/style/sass/variables";
@import "./../../common/style/sass/mixins";

.work {
    background-color: $primaryColor;
    @include flexPattern(flex, "", "", flex-start, column);
    text-align: left;
    margin: 10px;
    width: 300px;
    border-radius: 0 0 10px 10px;
    box-shadow: 20px 18px 12px #1c1e21;
    .workPreview {
        height: 200px;
        width: 100%;
        @include backgroundImgPattern();
        position: relative;
        /*& указывает на родителя .workPreview*/
        &::before {
            @include beforElement();
            top: 0;
            bottom: 0;
            left: 0;
            right: 0;
            opacity: 0.5;
        }
        .workPreviewButton {
            position: relative;
            background-color: $primaryColor;
            height: 25px;
            width: 100px;
            z-index: 1;
            opacity: 0.8;
            transition: transform 0.3s, opacity 0.3s;
            &:hover {
                transform: scale(2.15);
                opacity: 0;
                //transform or animation
                //animation-name: pulsing;
                //animation-duration: 1000ms;
                //animation-iteration-count: 1;
                //animation-timing-function: ease-in-out;
                //@keyframes pulsing {
                //    0% {
                //        padding: 10px 20px;
                //        left: 0;
                //        top: 0;
                //        height: 25px;
                //        width: 100px;
                //        opacity: 0.8;
                //    }
                //    100% {
                //        padding: 30px 44px;
                //        left: -24px;
                //        top: -20px;
                //        height: 30px;
                //        width: 100px;
                //        opacity: 0;
                //    }
                //}
            }
        }
        a {
            padding: 10px 20px;
            display: block;
            text-align: center;
            margin-top: 70px;
            margin-left: 80px;
        }
    }
    h3 {
        @include fontPattern(500, 24px, 32px);
        margin-bottom: 0;
        color: $textColor;
    }
    .description, h3 {
        color: $textColor;
        padding: 0 15px 0 18px;
        a {
            //margin: 10px;
        }
    }
}


